import React from 'react';



class Rimuovi extends React.Component{
	constructor(){
		super();
		this.state={
			showTags:false,
		}
	}

	showTagsChange = () => {

		this.setState((prevState, props) => {
			if(!prevState.showTags){
				window.mostraTags.innerHTML="Nascondi #Tags";
				return {showTags: true};
			}else{
				window.mostraTags.innerHTML="Mostra #Tags";
				return {showTags: false};
			}

			});
	}

	onSubmitFotoRemove = () =>{
		const bool = window.confirm('sicuro di voler rimuovere?üò¢');
		if(bool){

			fetch('https://agile-forest-45123.herokuapp.com/fotoRemove',{
			method:'POST',
			headers:{'Content-Type':'application/json'},
			body: JSON.stringify({id:this.props.currentId})
		}).then(resp=>resp.json())
		.then(resp=>{
			this.props.fetchFoto();
			this.props.outClick();
			console.log("eliminata");
			window.location.reload();//da fixare (reload when delete something)
		})
		}else console.log("non eliminata")
	}
	onClickTag = (e) =>{

		let testo = e.target.innerHTML.replace("#","")
		testo = testo.replace(" ","")
		window.barracerca.value = testo
		testo = " "+ testo;
		var gino2 = {value:testo};
		var testo3 = {target:gino2};
		this.props.outClick();
		this.props.onSearchChange(testo3)

		window.barracerca.classList.replace("opacitynullSearch","opacitytopSearch")
		window.titoloTop.classList.replace("opacitytopTitolo","opacitynullTitolo")
		window.Scrollamelo.scrollTop = 0;




	}


	render(){

		function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
		}

		const gino1 = window.document.getElementById(`img${this.props.currentId}`);
		let tags = "";
		let tagsArray =[];
		if(gino1)
			tags = gino1.attributes.tag.nodeValue
			let tagsMod = tags.replace("show","")
			tagsArray = tagsMod.split(" ");
			for(let i3=0;i3<tagsArray.length;i3++){
				if(tagsArray[i3]==="show"||tagsArray[i3]==="")
					tagsArray.splice(i3,1)
			}
			let tagsArrayFiltered = tagsArray.filter( onlyUnique );
			let stringaCompleta = "";
			for(let i4=0;i4<tagsArrayFiltered.length;i4++){
				stringaCompleta= stringaCompleta.concat("#")
				stringaCompleta= stringaCompleta.concat(tagsArrayFiltered[i4]+" ")
			}
			let stringaCompletaMod = stringaCompleta.split(" ");
			let stringaCompletaModArr = []
			for(let i5=0;i5<stringaCompletaMod.length-1;i5++){


				function getRandomHSL() {
				  var h = '';
					var s = '';
					h += Math.floor(Math.random() * 360);
 					s += 51 + Math.floor(Math.random() * 49);
				  return `hsla(${h}, ${s}%, 50%, 1)`;
				}

				let colore = getRandomHSL();
				let colore2 = colore.replace("50%","15%")
				var hashtagStyle = {
					color:colore,
					background:colore2,
					border: "2px solid "+colore,
    			borderRadius: "16px",
    			padding: "0px 10px 0px 4px",

				};
				stringaCompletaModArr.push(<span className={"hashtag"} style={hashtagStyle} onClick={this.onClickTag} key={`tagNr${i5}`}>{`${stringaCompletaMod[i5]}`} </span>)
			}






		var styleInline = {

		};
		if(this.props.type==="desk"){
			styleInline = {
				fontSize: "x-large",
			  margin:"5px",
			}
		}else{
			styleInline = {
			  margin:"5px",
			  fontSize: "small",
			}
		}


		let displaY="displayNone";
		let displaYMOD="displayNone";

		if(this.props.mod==="sfondogallery"&&this.props.display==="displayInline"&&this.props.amIOffline!==true)
			displaY="displayInline";
		if(this.props.mod==="sfondogallery"&&this.props.display==="displayInline")
			displaYMOD="displayInline";

			var h44 = document.createElement("h4");
			if(this.props.type!=="mobile"){


				h44.innerText="sei offlineüò±"
				h44.classList.add("h44")
				if(window.titoloTop){
					let titolo = document.getElementById("titoloTop")
						if(this.props.amIOffline===true&&(titolo.lastElementChild===null))
						if(this.props.amIOffline===true&&(titolo.parentElement.lastElementChild.id==="titoloTop")){
								titolo.parentElement.appendChild(h44);
							}
							//titolo.appendChild(imgg);
							//sei offline desk
				}
			}else{


				h44.innerText="sei offlineüò±"
				h44.classList.add("h44")
				if(window.titoloTop){
					let titolo = document.getElementById("titoloTop")

						if(this.props.amIOffline===true&&(titolo.parentElement.lastElementChild.id==="titoloTop")){
							titolo.parentElement.appendChild(h44);
						}
				}

			}










		let tagsJSX = []
		let tagsJSX2 = []
		if(this.state.showTags)
			tagsJSX.push(<div key="tags" className={`${displaYMOD} fadeIn tags pointer pl1 pr1 br2 zIndexhigh3`}>	<div style={styleInline} >{stringaCompletaModArr}</div></div>)

		let h3style = {}
		if(this.props.type!=="mobile")
			h3style = {fontSize: "x-large", marginTop:"1px",marginBottom:"5px"}
			else
			h3style = {fontSize:"small", margin: "5px", width: "90px",}

			tagsJSX2.push(<div key="mostraTag" onClick={this.showTagsChange } className={`${displaYMOD} showTags pointer  zIndexhigh3`}><h3 id="mostraTags" style={h3style}>Mostra #Tags</h3></div>)





			return(
				<div id="Rimuovi">
					<div onClick={this.onSubmitFotoRemove} className={`${displaY} eliminaImmagine pointer pl1 pr1 br2 zIndexhigh3`}>
						<h3 style={styleInline}>Elimina <span role="img" aria-label="X">‚ùå</span></h3>
					</div>
					<div onClick={this.props.outClick} className={`${displaYMOD} indietroCarousel pointer br2 zIndexhigh3`}>
						<h3 style={styleInline} >&lt;  Indietro</h3>
					</div>
					{
						tagsJSX
					}
					{
						tagsJSX2
					}
				</div>
				);



	}
}


export default Rimuovi;



